@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
iframe{display:none}.App{display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:"Open Sans", sans-serif}.App__title{font-weight:400}.wrapper{display:flex;justify-content:space-between;align-items:center}

.form{max-width:600px;padding:10px;display:flex;flex-direction:column;justify-content:center;align-items:baseline;background-color:#55c5aa;border-radius:10px}.form__input{margin-bottom:8px;width:100%}

.carousel{overflow:hidden;position:relative}.carousel__container{width:1300px;display:flex;flex-direction:column;justify-content:center;align-items:center}.carousel__list{display:flex;flex-direction:row;list-style:none;padding:0}

.button{display:flex;justify-content:center;align-items:center;font-size:30px;color:#000}.button__prev,.button__next{margin:10px;outline:none}

